// 存储空间管理接口
import http from '../httpConfig'
const table = 'mrbase_sys_datamodel_procedure'


// 运行储存过程语句
export const runSql = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/procedure/runSql/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

